    export default {
		onShow(){
		},
		mounted() {
			this.getDate();
		},
        data() {
            return {
				textData:"",
				img:"",
				title:"",
				name:","
            }
        },
		methods: {
			getDate(){
				uni.request({
				    url: 'http://yxx.h-etrip.com:9092/etrip/api/app/et/areas/2220',
				    success: (res) => {
				        this.textData = res.data.content.content;
						this.img = this.getImgUrl(res.data.content);
						this.title = res.data.content.summary;
						this.name = res.data.content.name;
				    }
				});
			},
			 getImgUrl(data) {
			      return this.getSrc(data);
			 },
		}
    }
